import { Component, OnInit, Input } from '@angular/core';
import { CarouselImage } from 'src/app/models/carousel_image.model';

@Component({
  selector: 'app-carousel-block',
  templateUrl: './carousel-block.component.html',
  styleUrls: ['./carousel-block.component.css']
})
export class CarouselBlockComponent implements OnInit {

  @Input()
  carouselImages: CarouselImage[];

  constructor() { }

  ngOnInit(): void {
    
    if(this.carouselImages.length == 0) {
      this.carouselImages.push(new CarouselImage(1, 'assets/carousel_1.jpg', 'A special gift, for a special women', 'Necklace', 'LEFT'));
      this.carouselImages.push(new CarouselImage(2, 'assets/carousel_2.jpg', 'Crafted specially for you', 'Bangles', 'LEFT'));
      this.carouselImages.push(new CarouselImage(3, 'assets/carousel_3.jpg', 'Gift someone', 'Rings', 'RIGHT'));
    }
  }
}
