import { Component, OnInit, Input } from '@angular/core';
import { NewArrivalSet } from 'src/app/models/new_arrivals_set.model';
import { Product } from 'src/app/models/product.model';

@Component({
  selector: 'app-new-arrivals-block',
  templateUrl: './new-arrivals-block.component.html',
  styleUrls: ['./new-arrivals-block.component.css']
})
export class NewArrivalsBlockComponent implements OnInit {

  @Input()
  newArrivals: Product[] = [];

  newArrivalsSet: NewArrivalSet[] = [];

  constructor() { }

  ngOnInit(): void {
    
    if (this.newArrivals.length == 0) {
      this.newArrivals.push(new Product(1, 'assets/new_arrivals/p1.png', 'Custom Design Ring 24 carets gold', 'rings', 1000, '', '', '', ''));
      this.newArrivals.push(new Product(2, 'assets/new_arrivals/p2.png', 'Double chain bracelet', 'chains', 100, '', '', '', ''));
      this.newArrivals.push(new Product(3, 'assets/new_arrivals/p5.png', 'Gold plated bangle pair', 'bangles', 95, '', '', '', ''));
    }
    this.newArrivalsSet.push(new NewArrivalSet(1, 'assets/new_arrivals/b1.png', 'Wedding Set'));
    this.newArrivalsSet.push(new NewArrivalSet(1, 'assets/new_arrivals/b2.png', 'Evening Dress Gold Set'));

  }

}
