import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { CarouselImage } from '../models/carousel_image.model';
import { Category } from '../models/category.model';
import { Product } from '../models/product.model';

@Injectable({
  providedIn: 'root'
})
export class HomeLoaderService {

  CAROUSEL_URL = 'homePageSlidingImages/getAllImages';
  CATEGORY_URL = 'categories/getAll';
  NEW_ARRIVAL_URL = 'products/getLatestThreeProducts';

  constructor(private http: HttpClient) { }

  carouselImages: CarouselImage[] = [];
  carouselImagesObserver = new Subject<boolean>();

  categoryModels: Category[] = [];
  categoryModelsObserver = new Subject<boolean>();

  newArrivals: Product[] = [];
  newArrivalsObserver = new Subject<boolean>();

  fetchCarouselImages() {
    this.http.get(this.CAROUSEL_URL).subscribe(
      (response : CarouselImage[]) => {
        this.carouselImages = response;
        this.carouselImagesObserver.next(true);
      },
      error => {
        this.carouselImages = [];
        this.carouselImagesObserver.next(true);
      }
    );
  }

  getCarouselImages() {
    return this.carouselImages.slice();
  }

  fetchCategoryModels() {
    this.http.get(this.CATEGORY_URL).subscribe(
      (response : Category[]) => {
        this.categoryModels = response;
        this.categoryModelsObserver.next(true);
      },
      error => {
        this.categoryModels = [];
        this.categoryModelsObserver.next(true);
      }
    );
  }

  getCategoryModels() {

    this.categoryModels.forEach(c => {
      if (c.categoryName.toLowerCase() === 'bangles')
        c.categoryID = 5;
      c.imagePath = 'assets/popular_collections/p' + c.categoryID + '.png';
      c.viewName = 'Popular ' + c.categoryName.substr(0,1).toUpperCase() + c.categoryName.substr(1).toLowerCase();
    });

    return this.categoryModels.slice();
  }

  fetchNewArrivals() {
    this.http.get(this.NEW_ARRIVAL_URL).subscribe(
      (response : Product[]) => {
        this.newArrivals = response;
        this.newArrivalsObserver.next(true);
      },
      error => {
        this.newArrivals = [];
        this.newArrivalsObserver.next(true);
      }
    );
  }

  getNewArrivals() {
    return this.newArrivals.slice();
  }

}
