import { Component, OnInit, Renderer2 } from '@angular/core';
import { HeaderOption } from 'src/app/models/header_options.model';

@Component({
  selector: 'app-bottom-header',
  templateUrl: './bottom-header.component.html',
  styleUrls: ['./bottom-header.component.css']
})
export class BottomHeaderComponent implements OnInit {

  activeHeader = 'home';
  hoveredHeader = 'home';

  optionsOpened = false;

  women: HeaderOption[];
  men: HeaderOption[];
  kids: string[]
  collections: String[]

  constructor(private render: Renderer2) { }


  ngOnInit(): void {

    this.women = [new HeaderOption('Necklaces', ['Necklace Sets', 'Patta Sets', 'Mangalsutras', 'Plain Chains', 'Fancy Chains', 'Pendant Sets', 'Pendants']),
                  new HeaderOption('Bangles', ['Arm Bracelets (Baju Bandh)', 'Bracelets', 'Bangle/Bracelet', 'Finger Bracelets', 'Set of Bangles', 'Kara']),
                  new HeaderOption('Earrings', ['Chandbali', 'Clipons', 'Hoops', 'Jumkha', 'Long Drops', 'Sui Dhaga', 'Tops']),
                  new HeaderOption('Rings', ['GIA Certified Solitaire', 'Fancy Rings', 'Bands', 'Designer Bands']),
                  new HeaderOption('Accessories', ['Anklets (Payal)', 'Maang Tikka', 'Nosepins', 'Sahara', 'Waist Band', 'Broach']),
                  new HeaderOption('Hot Deals', ['Online Deals', 'Deal of the Day'])];

    this.men = [new HeaderOption('Rings', ['Plain Bands', 'Designer Bands', 'Fancy']),
                new HeaderOption('Bracelets', ['Kara', 'Fancy Bracelets']),
                new HeaderOption('Chains', ['Fancy Chains'])]

    this.kids = ['Bangles', 'Bracelets', 'Earrings', 'Chains', 'Pendents', 'Rings']
    
    this.collections = ['Rangoli Collections', 'Utsava Collections', 'Dussera Collections', 'Diwali Collections']

  }

  setHovered(status: string) {
    this.hoveredHeader = status;

    const optionstab = document.getElementsByClassName('mega-dropdown-menu');

    if(status !== 'home') {
      if(status === 'womens') {
        this.render.setProperty(optionstab[0], 'style', 'left: 0px; margin-left: 0px');
      } else {
        let tab = (status === 'mens') ? 1 : (status === 'kids') ? 2 : 3;
        this.render.setProperty(optionstab[tab], 'style', 'margin-left: -1px');
      }
      this.optionsOpened = true;
    } 
  }

  removeHovered(status: string) {
    this.hoveredHeader = 'home';

    const optionstab = document.getElementsByClassName('mega-dropdown-menu');

    if (status !== 'home') {
      if(status === 'womens') {
        this.render.setProperty(optionstab[0], 'style', 'left: 0px; margin-left: 0px;');
      } else {
        let tab = (status === 'mens') ? 1 : (status === 'kids') ? 2 : 3;
        this.render.setProperty(optionstab[tab], 'style', '');
      }
      this.optionsOpened = false;
    }

  }

  openOrCloseOptions(status: string) {
    const optionstab = document.getElementsByClassName('mega-dropdown-menu');

    if(status !== 'home') {
      if(status === 'womens' && this.optionsOpened) {
        this.render.setProperty(optionstab[0], 'style', 'height: 0px; left: 0px; margin-left: 0px;');
      } else if(this.optionsOpened) {
        let tab = (status === 'mens') ? 1 : (status === 'kids') ? 2 : 3;
        this.render.setProperty(optionstab[tab], 'style', 'height: 0px;');
      }

      if(status === 'womens' && !this.optionsOpened) {
        this.render.setProperty(optionstab[0], 'style', 'left: 0px; margin-left: 0px;');
      } else if(!this.optionsOpened) {
        let tab = (status === 'mens') ? 1 : (status === 'kids') ? 2 : 3;
        this.render.setProperty(optionstab[tab], 'style', 'margin-left: -1px');
      }

      this.optionsOpened = !this.optionsOpened;
    }
  }
}
