import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-top-header',
  templateUrl: './top-header.component.html',
  styleUrls: ['./top-header.component.css']
})
export class TopHeaderComponent implements OnInit {

  loginBoxStatus = false;
  registerBoxStatus = false;

  constructor() { }

  ngOnInit(): void {
  }

  openLoginBox(status: boolean) {
    this.loginBoxStatus = status;
    this.registerBoxStatus = false;
  }

  openRegisterBox(status: boolean) {
    this.registerBoxStatus = status;
    this.loginBoxStatus = false;
  }

  closeBoxes() {
    this.loginBoxStatus = false;
    this.registerBoxStatus = false;
  }

}
