import { Component, OnInit } from '@angular/core';
import { HomeLoaderService } from '../services/home-loader.service';
import { CarouselImage } from '../models/carousel_image.model';
import { Subscription } from 'rxjs';
import { Category } from '../models/category.model';
import { Product } from '../models/product.model';

@Component({
  selector: 'app-body-blocks',
  templateUrl: './body-blocks.component.html',
  styleUrls: ['./body-blocks.component.css']
})
export class BodyBlocksComponent implements OnInit {

  constructor(private homeLoaderService: HomeLoaderService) { }

  carouselImages: CarouselImage[] = [];
  carouselImagesLoaded = false;

  categoryModels: Category[] = [];
  categoryModelsLoaded = false;

  newArrivals: Product[] = [];
  newArrivalsLoaded = false;

  ngOnInit(): void {

    this.homeLoaderService.fetchCarouselImages();
    this.homeLoaderService.carouselImagesObserver.subscribe(
      (carouselImagesLoaded: boolean) => {
        this.carouselImages = this.homeLoaderService.getCarouselImages();
        this.carouselImagesLoaded = carouselImagesLoaded;
      }
    );

    this.homeLoaderService.fetchCategoryModels();
    this.homeLoaderService.categoryModelsObserver.subscribe(
      (categoryModelsLoaded: boolean) => {
        this.categoryModels = this.homeLoaderService.getCategoryModels();
        this.categoryModelsLoaded = categoryModelsLoaded;
      }
    );

    this.homeLoaderService.fetchNewArrivals();
    this.homeLoaderService.newArrivalsObserver.subscribe(
      (newArrivalsLoaded: boolean) => {
        this.newArrivals = this.homeLoaderService.getNewArrivals();
        this.newArrivalsLoaded = newArrivalsLoaded;
      }
    );

  }

}
