import { Component, OnInit, Input } from '@angular/core';
import { Category } from 'src/app/models/category.model';

@Component({
  selector: 'app-popular-collection-block',
  templateUrl: './popular-collection-block.component.html',
  styleUrls: ['./popular-collection-block.component.css']
})
export class PopularCollectionBlockComponent implements OnInit {

  @Input()
  popularCollections: Category[] = [];

  constructor() { }

  ngOnInit(): void {

    if (this.popularCollections.length == 0) {
      this.popularCollections.push(new Category(1, 'assets/popular_collections/p1.png', 'rings', 'Popular Rings'));
      this.popularCollections.push(new Category(2, 'assets/popular_collections/p2.png', 'chains', 'Popular Chain'));
      this.popularCollections.push(new Category(3, 'assets/popular_collections/p3.png', 'pendents', 'Popular Pendents'));
      this.popularCollections.push(new Category(4, 'assets/popular_collections/p5.png', 'bangles', 'Popular Bangles'));
    }
  }

}
